body {
  font-family: 'Roboto', sans-serif;
  color: #141414;
  line-height: 1;
  font-size: 14px;
  background: #ffffff;
}

.wrap {
  padding-left: 12px;
  padding-right: 12px;
  box-sizing: border-box;
}

.header {
  padding-top: 24px;
  padding-bottom: 24px;
}

.header__wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  text-align: center;
  position: relative;
}

.header__logo {
  width: 100%;
}

.header__logo-name {
  margin: 0;
}

.header__logo-description {
  font-size: 10px;
  opacity: 0.65;
  max-width: 200px;
  margin: 0 auto;
}

.header__actions {
  display: none;
}

.header__contacts {
  display: none;
}

.header__phone-number {
  display: inline-flex;
  margin-right: 12px;
  color: #141414;
  font-size: 14px;
  font-family: 'Roboto Slab', sans-serif;
  letter-spacing: 0.5px;
}

.header__phone-icon {
  margin: 0;
}

.header__personal {
  position: absolute;
  top: -8px;
  left: 0;
}

.header__cart-icon {
  margin: 0;
}

.header__cart-num {
  position: absolute;
  top: -4px;
  right: -16px;
  background: #FF006C;
  color: #ffffff;
  font-size: 12px;
  width: 24px;
  height: 24px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  border-radius: 50px;
  padding: 4px;
  text-align: center;
  font-weight: 600;
  font-family: 'Roboto Slab', sans-serif;
  letter-spacing: 0.5px;
}

.header__menu {
  position: absolute;
  top: -8px;
  right: 0;
}

.header__menu-icon {
  margin: 0;
}

.header__menu-text {
  font-size: 12px;
  font-family: 'Roboto Slab', sans-serif;
}

.triggers {
  background: #E8EDF1;
  padding-top: 20px;
  padding-bottom: 20px;
}

.triggers__wrap {
  border-top: 1px solid #C5CDD3;
  border-bottom: 1px solid #C5CDD3;
  padding-top: 48px;
  padding-bottom: 48px;
}

.triggers__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
}

.triggers__item {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  text-align: center;
  max-width: 420px;
}

.triggers__item:not(:last-child) {
  margin-bottom: 60px;
}

.triggers__item-icon {
  margin: 0;
}

.triggers__item-icon picture {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.triggers__item-icon picture:before {
  background: #FF006C;
  border-radius: 50px;
  width: 32px;
  height: 32px;
  position: absolute;
  top: -10px;
  left: -20px;
  content: '';
  display: block;
  z-index: 0;
}

.triggers__item-icon picture img {
  position: relative;
  z-index: 1;
}

.triggers__item-info {
  margin-top: 24px;
  width: 100%;
}

.triggers__item-name {
  font-weight: 500;
  font-size: 14px;
  margin-bottom: 8px;
  font-family: 'Roboto Slab', sans-serif;
}

.triggers__item-text {
  font-size: 12px;
  margin-bottom: 12px;
  opacity: 0.7;
  line-height: 1.5;
}

.triggers__item-link {
  display: inline;
  font-size: 12px;
  color: #FF006C;
}

.catalog__top {
  padding-top: 40px;
  padding-bottom: 40px;
}

.catalog__top-title {
  font-size: 24px;
  text-transform: uppercase;
  font-weight: 500;
  font-family: 'Roboto Slab', sans-serif;
  margin-bottom: 24px;
}

.catalog__sections {
  margin-bottom: 80px;
  padding-top: 32px;
  box-sizing: border-box;
}

.catalog__section:not(:last-child) {
  margin-bottom: 80px;
}

.catalog__section-link {
  display: block;
}

.catalog__section-image {
  margin: 0;
  background: #eeeeee;
  position: relative;
}

.catalog__section-image:before {
  position: absolute;
  left: 16px;
  right: 16px;
  top: -16px;
  border-radius: 8px;
  background: #FF006C;
  z-index: 0;
  content: '';
  display: block;
  height: 100%;
}

.catalog__section-image picture {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding-top: 50%;
  background-color: #ffffff;
  position: relative;
  box-sizing: border-box;
  z-index: 1;
  border-radius: 4px;
  overflow: hidden;
}

.catalog__section-image picture img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
}

.catalog__section-content {
  padding-left: 16px;
  padding-right: 16px;
  box-sizing: border-box;
  margin-top: 24px;
}

.catalog__section-title {
  font-size: 20px;
  font-weight: 500;
  font-family: 'Roboto Slab', sans-serif;
  text-decoration: none;
  color: #141414;
}

.catalog__section-list {
  margin-top: 24px;
}

.catalog__section-item {
  line-height: 1.5;
}

.catalog__section-item:not(:last-child) {
  margin-bottom: 12px;
}

.catalog__section-item a {
  color: #141414;
  font-size: 13px;
  opacity: 0.65;
  padding: 2px 0;
}

.catalog__section-count {
  font-size: 11px;
  opacity: 0.3;
}

.catalog__section-count:before {
  content: '(';
}

.catalog__section-count:after {
  content: ')';
}

.catalog__reviews {
  padding-top: 40px;
  box-sizing: border-box;
}

.catalog__reviews-head {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}

.catalog__reviews-title {
  font-size: 16px;
  text-transform: uppercase;
  font-weight: 500;
  font-family: 'Roboto Slab', sans-serif;
  margin-bottom: 24px;
  position: relative;
  width: 100%;
}

.catalog__reviews-title:after {
  width: 100%;
  height: 1px;
  content: '';
  display: inline-flex;
  position: relative;
  z-index: 0;
  background: #FF006C;
}

.catalog__review:not(:last-child) {
  margin-bottom: 40px;
}

.catalog__review-mark {
  margin: 0 0 4px;
}

.catalog__review-head {
  margin-bottom: 12px;
}

.catalog__review-name {
  font-size: 14px;
  font-weight: 500;
  font-family: 'Roboto Slab', sans-serif;
}

.catalog__review-date {
  font-size: 12px;
  opacity: 0.3;
  margin-top: 8px;
}

.catalog__review-text {
  font-size: 14px;
  opacity: 0.65;
  line-height: 1.75;
}

.catalog__items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -12px;
  margin-right: -12px;
}

.catalog__item {
  margin: 12px;
  padding: 12px;
  border: 1px solid #eeeeee;
  box-sizing: border-box;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.catalog__item-wrap {
  display: flex;
  flex-wrap: wrap;
  height: 100%;
  width: 100%;
}

.catalog__item-image-link {
  display: block;
}

.catalog__item-figure {
  margin: 0;
  background: #eeeeee;
  width: 100%;
}

.catalog__item-figure picture {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding-top: 100%;
  background-color: #ffffff;
  position: relative;
  box-sizing: border-box;
}

.catalog__item-figure picture img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  position: absolute;
  top: 0;
  left: 0;
}

.catalog__item-info {
  margin-top: 16px;
  width: 100%;
  margin-bottom: 8px;
}

.catalog__item-title {
  font-size: 13px;
  font-weight: 500;
  font-family: 'Roboto Slab', sans-serif;
  line-height: 1.25;
}

.catalog__item-title-link {
  display: block;
  color: #141414;
}

.catalog__item-actions {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  margin-top: 16px;
  width: 100%;
}

.catalog__item-oldprice {
  font-size: 12px;
  opacity: 0.65;
}

.catalog__item-curprice {
  font-size: 14px;
  font-weight: 500;
  font-family: 'Roboto Slab', sans-serif;
  color: #FF006C;
}

.catalog__item-addtocart {
  display: inline-flex;
  padding: 8px 16px;
  text-align: center;
  justify-content: center;
  box-sizing: border-box;
  border-radius: 50px;
  border: 1px solid #FF006C;
  font-size: 10px;
  color: #141414;
  text-decoration: none;
}

.sections {
  padding-top: 40px;
  padding-bottom: 40px;
  box-sizing: border-box;
}

.sections__info {
  margin-bottom: 24px;
}

.sections__info-title {
  font-size: 24px;
  text-transform: uppercase;
  font-weight: 500;
  font-family: 'Roboto Slab', sans-serif;
}

.sections__info-text {
  font-size: 14px;
  opacity: 0.65;
  line-height: 1.5;
  margin-top: 12px;
}

.sections__list {
  margin-left: -12px;
  margin-right: -12px;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;
}

.sections__item-figure {
  margin: 0;
  background: #eeeeee;
  border-radius: 4px;
  overflow: hidden;
}

.sections__item-figure picture {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding-top: 70%;
  background-color: #ffffff;
  position: relative;
  box-sizing: border-box;
}

.sections__item-figure picture img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
}

.sections__item-info {
  padding: 16px;
  box-sizing: border-box;
}

.sections__item-title {
  font-size: 16px;
  font-weight: 500;
  font-family: 'Roboto Slab', sans-serif;
}

.sections__item-title a {
  text-decoration: none;
  color: #141414;
}

.sections__item-count {
  font-size: 12px;
  opacity: 0.65;
  font-weight: 400;
  font-family: 'Roboto', sans-serif;
}

.sections__item-text {
  margin-top: 12px;
  font-size: 14px;
  opacity: 0.65;
  line-height: 1.5;
}

.products__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: stretch;
  margin-left: -12px;
  margin-right: -12px;
}

.product__image {
  margin: 0;
  background: #eeeeee;
  width: 100%;
}

.product__image picture {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding-top: 100%;
  background-color: #ffffff;
  box-sizing: border-box;
  position: relative;
}

.product__image picture img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  position: absolute;
  top: 0;
  left: 0;
}

.product__image picture img-figure {
  margin: 0;
  background: #eeeeee;
  width: 100%;
}

.product__thumbs {
  display: flex;
  margin-top: 40px;
}

.product__thumb {
  width: 80px;
  height: 80px;
}

.product__thumb:not(:last-child) {
  margin-right: 12px;
}

.product__thumb-image {
  margin: 0;
  display: block;
  width: 100%;
  height: 100%;
  border: 1px solid #eeeeee;
}

.product__thumb-image picture {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 8px;
  box-sizing: border-box;
}

.product__thumb-image picture img {
  max-width: 100%;
  max-height: 100%;
  display: block;
}

.product__content {
  margin-top: 60px;
}

.product__category {
  text-align: center;
}

.product__category-text {
  opacity: 0.5;
  font-size: 12px;
  margin-bottom: 2px;
}

.product__category-title {
  color: #fe2c6c;
  font-size: 16px;
  text-transform: uppercase;
  border-bottom: 1px solid;
  display: inline-block;
  padding: 2px;
}

.product__price {
  margin-top: 40px;
  text-align: center;
}

.product__price-base {
  margin-bottom: 24px;
}

.product__price-base-title {
  font-size: 12px;
  opacity: 0.65;
  margin-bottom: 4px;
  width: 100%;
}

.product__price-wholesale-title {
  font-size: 16px;
  font-family: 'Roboto Slab', sans-serif;
  margin-bottom: 8px;
  width: 100%;
}

.product__price-current {
  font-weight: 500;
  font-family: 'Roboto Slab', serif;
  color: #FF006C;
  font-size: 40px;
}

.product__price-old {
  display: block;
  font-size: 20px;
  opacity: 0.65;
  font-family: 'Roboto Slab', sans-serif;
  margin-bottom: 4px;
  margin-right: 12px;
}

.product__price-value {
  display: flex;
  flex-wrap: wrap;
  align-items: baseline;
}

.product__actions {
  margin-top: 40px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.product__action {
  background: #ff006c;
  color: #ffffff;
  font-size: 18px;
  font-weight: 500;
  font-family: 'Roboto Slab', sans-serif;
  display: inline-flex;
  border-radius: 50px;
  padding: 14px 24px;
  text-decoration: none;
  width: 100%;
  max-width: 320px;
  box-sizing: border-box;
  align-items: center;
  justify-content: space-between;
}

.product__action:before {
  display: inline-flex;
  width: 8px;
  height: 8px;
  background: #ffffff;
  border-radius: 50%;
  content: '';
}

.product__action:after {
  display: inline-flex;
  width: 8px;
  height: 8px;
  background: #ffffff;
  border-radius: 50%;
  content: '';
}

.product__info {
  margin-top: 60px;
}

.product__info-title {
  font-family: 'Roboto Slab', sans-serif;
  font-weight: 500;
  text-transform: uppercase;
  font-size: 16px;
  border-bottom: 1px solid #FF006C;
  padding-bottom: 4px;
  margin-bottom: 20px;
  text-align: center;
}

.product__info-item {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  border-bottom: 1px dotted #dcdcdc;
}

.product__info-item:not(:last-child) {
  margin-bottom: 20px;
}

.product__info-prop {
  opacity: 0.65;
  font-size: 14px;
}

.product__info-value {
  font-size: 14px;
}

@media only screen and (min-width: 414px) {
  .catalog__item {
    width: calc(100% / 2 - 24px);
  }

  .sections__item-info {
    margin-top: 8px;
  }
}

@media only screen and (min-width: 425px) {
  .wrap {
    padding-left: 20px;
    padding-right: 20px;
  }

  .catalog__section-content {
    padding-left: 40px;
    padding-right: 40px;
  }
}

@media only screen and (min-width: 568px) {
  .catalog__sections-list {
    margin-left: -20px;
    margin-right: -20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: flex-start;
  }

  .catalog__section {
    width: calc(100% / 2 - 40px);
    margin-left: 20px;
    margin-right: 20px;
  }

  .catalog__section-content {
    padding-left: 16px;
    padding-right: 16px;
  }

  .sections__item {
    width: calc(100% / 2 - 24px);
    margin: 12px;
  }
}

@media only screen and (min-width: 768px) {
  .wrap {
    padding-left: 40px;
    padding-right: 40px;
  }

  .header__wrap {
    width: 100%;
    justify-content: flex-start;
    text-align: left;
    align-items: center;
  }

  .header__logo {
    width: auto;
    padding-right: 40px;
    box-sizing: border-box;
    margin-top: 8px;
  }

  .header__logo-description {
    margin-left: 0;
    font-size: 12px;
  }

  .header__actions {
    display: flex;
    width: 25%;
  }

  .header__action {
    display: inline-flex;
    padding: 12px 24px;
    border-radius: 50px;
    box-sizing: border-box;
    border: 1px solid #FF006C;
    font-size: 14px;
    text-decoration: none;
    color: #141414;
  }

  .header__personal {
    position: relative;
    top: 0;
    margin-right: 40px;
    margin-left: auto;
  }

  .header__menu {
    position: relative;
    top: 0;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: flex-start;
    margin-left: auto;
  }

  .header__menu-text {
    padding-left: 12px;
    box-sizing: border-box;
    font-size: 14px;
  }

  .catalog__section:not(:last-child) {
    margin-bottom: 120px;
  }

  .catalog__reviews-list {
    display: flex;
    flex-wrap: wrap;
    margin-left: -20px;
    margin-right: -20px;
  }

  .catalog__review {
    width: calc(100% / 2 - 40px);
    margin-left: 20px;
    margin-right: 20px;
  }

  .catalog__item {
    width: calc(100% / 3 - 24px);
    padding: 20px;
  }

  .catalog__item-curprice {
    font-size: 16px;
  }

  .catalog__item-addtocart {
    font-size: 12px;
  }

  .products .catalog__item {
    width: calc(100% / 3 - 24px);
    padding: 20px;
  }

  .products__wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
  }

  .products__section {
    width: calc(100% - 200px - 80px);
    margin-left: 40px;
  }

  .products__aside {
    width: 200px;
    margin-right: 40px;
  }

  .product__wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .product__images {
    width: 50%;
  }

  .product__thumb-image picture {
    padding: 16px;
  }

  .product__content {
    width: 50%;
    padding-left: 80px;
    box-sizing: border-box;
  }

  .product__category {
    text-align: left;
  }

  .product__price {
    text-align: left;
  }

  .product__actions {
    justify-content: flex-start;
  }

  .product__info-title {
    text-align: left;
    margin-bottom: 40px;
  }
}

@media only screen and (min-width: 1024px) {
  .header__contacts {
    display: flex;
    align-items: center;
  }

  .header__phone {
    display: flex;
    align-items: center;
    text-decoration: none;
  }

  .triggers__item {
    width: calc(100% / 3);
    padding-left: 24px;
    padding-right: 24px;
    box-sizing: border-box;
  }

  .triggers__item:not(:last-child) {
    margin-bottom: 0;
  }

  .catalog__top {
    padding-top: 80px;
  }

  .catalog__top-title {
    font-size: 40px;
  }

  .catalog__section {
    width: calc(100% / 3 - 40px);
  }

  .catalog__review {
    width: calc(100% / 3 - 40px);
  }

  .catalog__item {
    width: calc(100% / 4 - 24px);
  }

  .sections {
    padding-top: 80px;
  }

  .sections__info-title {
    font-size: 40px;
  }

  .sections__list {
    margin-left: -20px;
    margin-right: -20px;
  }

  .sections__item {
    width: calc(100% / 3 - 40px);
    margin: 40px 20px;
  }

  .products .catalog__item {
    width: calc(100% / 3 - 24px);
  }
}

@media only screen and (min-width: 1280px) {
  .header__logo {
    width: 315px;
  }

  .header__actions {
    width: 315px;
    margin-right: auto;
  }

  .catalog__page-wrap {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: row-reverse;
  }

  .catalog__sections {
    width: calc(100% - 300px - 80px);
    margin-left: 40px;
    padding-top: 40px;
  }

  .catalog__section-content {
    padding-left: 40px;
    padding-right: 40px;
  }

  .catalog__aside {
    width: 300px;
    margin-right: 40px;
  }

  .catalog__reviews-title {
    font-size: 24px;
  }

  .catalog__review {
    width: 100%;
  }

  .catalog__item {
    width: calc(100% / 5 - 24px);
  }

  .catalog__item-info {
    margin-top: 24px;
  }

  .catalog__item-curprice {
    font-size: 18px;
  }

  .sections__item {
    width: calc(100% / 4 - 40px);
  }

  .sections__item-title {
    font-size: 18px;
    line-height: 1.5;
  }

  .products .catalog__item {
    width: calc(100% / 4 - 24px);
  }

  .products__section {
    width: calc(100% - 300px - 80px);
  }

  .products__aside {
    width: 300px;
  }

  .product {
    padding-left: 40px;
    padding-right: 40px;
  }
}

@media only screen and (min-width: 1440px) {
  .header__phone-number {
    font-size: 16px;
  }

  .header__cart-num {
    font-size: 14px;
    width: 28px;
    height: 28px;
    top: -8px;
    right: -20px;
  }

  .header__menu-text {
    font-size: 16px;
  }

  .triggers__list {
    margin-left: -20px;
    margin-right: -20px;
  }

  .triggers__item {
    width: calc(100% / 3 - 40px);
    justify-content: flex-start;
    text-align: left;
    margin-left: 20px;
    margin-right: 20px;
  }

  .triggers__item-icon {
    width: 80px;
  }

  .triggers__item-info {
    width: calc(100% - 80px);
    margin-top: 0;
    padding-left: 12px;
    box-sizing: border-box;
    padding-right: 24px;
  }

  .triggers__item-name {
    font-size: 16px;
  }

  .triggers__item-text {
    font-size: 14px;
  }

  .triggers__item-link {
    font-size: 14px;
  }

  .catalog__top {
    padding-top: 120px;
    padding-bottom: 120px;
  }

  .catalog__top-title {
    font-size: 60px;
  }

  .sections {
    padding-top: 120px;
    padding-bottom: 120px;
  }

  .sections__info-title {
    font-size: 60px;
  }

  .product {
    padding-left: 80px;
    padding-right: 80px;
  }

  .product__images {
    width: 40%;
  }

  .product__content {
    width: 60%;
    padding-left: 120px;
    padding-top: 40px;
  }
}

@media only screen and (min-width: 1680px) {
  .wrap {
    padding-left: 80px;
    padding-right: 80px;
    max-width: 1580px;
    margin: 0 auto;
  }
}