    font-size: 18px;
    font-weight: 500;
    font-family: 'Roboto Slab', sans-serif;
    display: inline-flex;
    border-radius: 50px;
    padding: 14px 24px;
    text-decoration: none;
    width: 100%;
    max-width: 320px;
    box-sizing: border-box;
    align-items: center;
    justify-content: space-between;

    &:before {
      display: inline-flex;
      width: 8px;
      height: 8px;
      background: #ffffff;
      border-radius: 50%;
      content: ''; }

    &:after {
      display: inline-flex;
      width: 8px;
      height: 8px;
      background: #ffffff;
      border-radius: 50%;
      content: ''; } }

  &__info {
    margin-top: 60px;

    &-title {
      font-family: 'Roboto Slab', sans-serif;
      font-weight: 500;
      text-transform: uppercase;
      font-size: 16px;
      border-bottom: 1px solid #FF006C;
      padding-bottom: 4px;
      margin-bottom: 20px;
      text-align: center;
      @media only screen and (min-width: 768px) {
        text-align: left;
        margin-bottom: 40px; } }

    &-item {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      border-bottom: 1px dotted #dcdcdc;

      &:not(:last-child) {
        margin-bottom: 20px; } }

    &-prop {
      opacity: 0.65;
      font-size: 14px; }

    &-value {
      font-size: 14px; } } }
